@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-content{
    height: 100px;
}
.carousel{
    margin:0;
    padding:0;
    height:500px;
    perspective:250px;
}
.carousel .carousel-item{
    width:200px;
    margin-top:auto;
}

.carousel .carousel-item img{
    width:100%;
}

small{
    display:flex;
    justify-content:center;

}

.material-icons{
    display: inline-flex;
    vertical-align: top;
}

.enrol{
    margin-top:0;
}

.halfway-fab{
    width:75px;
}
.modal-trigger{
    margin-right:0;
}

.modal-footer{
    align-items: center;
    display:flex;
    justify-content:center;
    margin-top:25px;
}

@media screen and (max-width: 400px) {
    .modal-footer{
        margin-top:60px;
    }
}

.slide{
    max-width: 1100px;
    display: flex;
}
.wrapper{
  width: 100%;
  display: flex;
  align-items: center;
}

.carousel{
  max-width: 1200px;
  margin: auto;
  padding: 0 30px;
}

.carousel .carw{
    margin: 20px 0;
  line-height: 250px;
  font-size: 90px;
  font-weight: 600;
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba(0,0,0,0.2);
}
h2{
    margin-left:10%;
}
